import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
const EngineContainer = resolve => require(['../EngineContainer.vue'], resolve)
const DecisionFlow = resolve => require(['../DecisionFlow.vue'], resolve)

export default new Router({
  routes: [
    {
      path: '/engine',
      name: 'engine',
      component: EngineContainer,
      children: [
        {
          path: '/',
          redirect: 'decision-flow'
        },
        {
          path: 'decision-flow',
          name: 'decision-flow',
          component: DecisionFlow
        }
      ]
    }
  ]
})



